import {aadirCliente} from "../firebase/auth";

import React from 'react';
import { Modal, Stack, Form, Button } from 'react-bootstrap'; 

function ModalAadir({isModalAadir, setIsModalAadir, actualizarListaClientes}) {
  
  function aadirClienteModal() {
    //obtener la informacin del formulario
    const sku = document.getElementById("sku").value;
    const nombre = document.getElementById("nombre").value;
    console.log(sku, nombre);
 
    //enviar la informacin a firebase
    const infoCliente = {sku, nombre}
    aadirCliente(infoCliente);
    
    //cerrar el modal (false)
    actualizarListaClientes();
    setIsModalAadir(false);

  }

  return (
    <Modal show={isModalAadir} onHide={() => setIsModalAadir(false)}>
      <Modal.Header>
        <Modal.Title>Aadir Cliente</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Stack>
            <Form.Control id='sku' placeholder='Id' type='texto' className='mb-2'></Form.Control>
            <Form.Control id='nombre' placeholder='Nombre' type='texto'  className='mb-2'></Form.Control>
          </Stack>
        </Form>
      </Modal.Body>
      
      <Modal.Footer>
        <Button variant='secondary' onClick={() => setIsModalAadir(false)}>Cancelar</Button>
        <Button variant='primary' onSubmit={aadirClienteModal}>Registrar</Button>
      </Modal.Footer>

    </Modal>
  );
}

export default ModalAadir;