import {loginEmailPassword} from "../firebase/auth";

import React from "react";

import mision from "../images/mision.png";
import { Header } from "../components/Header";

export function Login() {
  
  async function submitHandler(e){
    e.preventDefault();
    const correo = document.getElementById("email").value;
    const contrasea = e.target.contrasea.value;
    // console.log(correo, contrasea);
    await loginEmailPassword(correo, contrasea);

  }
  
  
  
  
  return (
    <div className="loginContainer">
      <header>
        <Header />
      </header>
      <main className="loginMain">
        <img
          src={mision}
          className="misionInvested"
          alt="Contiene la misin de Invested"
        />
        <p className="iniciarSesion">Iniciar sesin</p>
        
        <form onSubmit={submitHandler} className="form" >
         <input type="text" name="email" placeholder="correo electrnico" className="email" id="email"/>
         <input type="password" name="contrasea" placeholder="contrasea" className="contrasea" id="contrasea"/>
         <button type="submit" className="button">Inicar sesin</button>
        </form>
                 
       
      </main>
      <footer className="loginFooter">
        <p className="footer">
          Inves+ed - Todos los derechos reservados. Aviso de privacidad
        </p>
      </footer>
    </div>
  );
}
